import { createMuiTheme } from '@material-ui/core/styles'
import { red } from '@material-ui/core/colors'

const value = {
  xs: 0,
  sm: 600,
  md: 960,
  lg: 1280,
  xl: 1920
}
const unit = 'px'
const step = 5

// Create a theme instance.
const theme = createMuiTheme({
  grid: {
    template: {
      test: {
        [`@media (min-width:${value.xs}${unit})`]: {
          gridTemplateColumns: `[full-start] 16px [center-start] repeat(4, [col-start] 1fr [col-end]) [center-end] 16px[full-end]`
        },
        [`@media (min-width:${value.sm}${unit})`]: {
          background: '#f00'
        },
        [`@media (min-width:${value.md}${unit})`]: {
          background: '#0f0'
        },
        [`@media (min-width:${value.lg}${unit})`]: {
          background: '#00f'
        },
        [`@media (min-width:${value.xl}${unit})`]: {
          background: '#ff0'
        }
      },
      columns:
        '[full-start] 1fr [center-start] repeat(8, [col-start] 8rem [col-end]) [center-end] 1fr [full-end]'
    }
  }
})

export default theme
